@import '../../styles/shared/index.scss';

$mobile-max-width: 960px;

.about {
    @include displayFlex();
    flex-direction: row;
    
    &__container-left {
        flex: 1 1 0;

        &__woman {
            width: 100%;
            height: auto;
            
            #left-arm, #left-sleeve {
                translate: -50px -178px;
                transform-origin: 50% 100%;
                animation: rotate 5s 0s infinite;

                @keyframes rotate {
                    0% {
                        transform: translate(0%, 0%) rotate(0deg);
                    }
                    50% {
                        transform: translate(0%, 0%) rotate(8deg);
                    }
                    100% {
                        transform: translate(0%, 0%) rotate(0deg);
                    }
                }
            }
        }
    }

    &__container-right {
        flex: 1 1 0;
        margin-left: 3em;
        margin-right: 2em;
        align-self: center;
    }

    @media only screen and (max-width: $mobile-max-width) {
        flex-direction: column;
        
        &__container-left {
            max-height: 15em !important; //SVG images rendering with huge space above and below IOS
        }

        &__container-right {
            margin: 2em 2em 0 2em;
        }
    }
}